#inputCity {
    background-image: url('images/weatherLandscape2.jpg');
    background-attachment: local;
    background-repeat: no-repeat;
    background-position: top;
    overflow: hidden;
    background-size: cover;
    position: absolute;
    
}

.darkMode {
    background-color: #181e23;
    color: white;
    margin-top: 0;
    transition: .5s;
}
.darkmode #modeBtn {
    background-color: white;
    color: #181e23;
    transition: .5s;
}
.lightMode {
    background-color: #fff;
    color: #181e23;
    transition: .5s;
}
.lightMode #modeBtn {
    background-color:#181e23;
    color:white;
    transition: .5s;
}